.page {
  body,
  input,
  textarea {
    cursor: url(./images/cursor.png), auto;
  }

  scroll-behavior: smooth;
  user-select: none;
  &__body {
    margin: 0;
    min-width: 320px;
  }

  &__menu {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;

    transition: all 0.3s;
    transform: translateX(-100%);
    opacity: 0;

    pointer-events: none;

    &:target {
      opacity: 1;
      transform: translateX(0);
      pointer-events: all;
    }
  }
  &:has(.page__menu:target) {
    overflow: hidden;
  }
}
